import Collapse from '@/components/Collapse'
export default {
  components: {
    Collapse
  },
  data () {
    return {
      isActive: [0, 0, 0, 0, 0, 0]
    }
  },
  beforeRouteEnter (to, from, next) {
    document.title = '帮助设置'
    next()
  },
  methods: {
    click (index) {
      this.isActive[index] = this.isActive[index] === 0 ? 1 : 0
      this.$forceUpdate()
    }
  }
}



